import React from "react";
import Button from "@material-ui/core/Button";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardHeader from "@material-ui/core/CardHeader";
import Grid from "@material-ui/core/Grid";
import StarIcon from "@material-ui/icons/StarBorder";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";

const useStyles = makeStyles((theme) => ({
  "@global": {
    ul: {
      margin: 0,
      padding: 0,
      listStyle: "none",
    },
  },
  heroContent: {
    padding: theme.spacing(4, 0, 2),
  },
  cardHeader: {
    backgroundColor:
      theme.palette.type === "light"
        ? theme.palette.grey[200]
        : theme.palette.grey[700],
  },
  cardPricing: {
    display: "flex",
    justifyContent: "center",
    alignItems: "baseline",
    marginBottom: theme.spacing(2),
  },
  footer: {
    borderTop: `1px solid ${theme.palette.divider}`,
    marginTop: theme.spacing(8),
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3),
    [theme.breakpoints.up("sm")]: {
      paddingTop: theme.spacing(6),
      paddingBottom: theme.spacing(6),
    },
  },
  namad: {
    cursor: "pointer",
  },
}));

const tiers = [
  {
    title: "رایگان",
    price: "0",
    duration: "3",
    description: ["تمامی امکانات سایت"],
    buttonText: "مهمان ما باشید",
    buttonVariant: "outlined",
  },
  {
    title: "سه‌ماهه",
    subheader: "پرطرفدار",
    price: "150000",
    duration: "90",
    description: ["تمامی امکانات سایت"],
    buttonText: "خرید",
    buttonVariant: "contained",
  },
  {
    title: "یک‌ماهه",
    price: "80000",
    duration: "30",
    description: ["تمامی امکانات سایت"],
    buttonText: "خرید",
    buttonVariant: "outlined",
  },
];

export default function Pricing() {
  const classes = useStyles();

  return (
    <React.Fragment>
      {/* Hero unit */}
      <Container maxWidth="sm" component="main" className={classes.heroContent}>
        <Typography
          component="h1"
          variant="h2"
          align="center"
          color="textPrimary"
          gutterBottom
        >
          اشتراک
        </Typography>
        <Typography
          variant="h5"
          align="center"
          color="textSecondary"
          component="p"
        >
          برای خرید عضویت سایت ملودیشن و استفاده کامل از امکانات سایت از فرم زیر
          اقدام نمایید.
        </Typography>
      </Container>
      {/* End hero unit */}
      <Container maxWidth="md" component="main">
        <Grid container spacing={5} alignItems="flex-end">
          {tiers.map((tier) => (
            // Enterprise card is full width at sm breakpoint
            <Grid
              item
              key={tier.title}
              xs={12}
              sm={tier.title === "Enterprise" ? 12 : 6}
              md={4}
            >
              <Card>
                <CardHeader
                  title={tier.title}
                  subheader={tier.subheader}
                  titleTypographyProps={{ align: "center" }}
                  subheaderTypographyProps={{ align: "center" }}
                  action={tier.title === "Pro" ? <StarIcon /> : null}
                  className={classes.cardHeader}
                />
                <CardContent>
                  <div className={classes.cardPricing}>
                    <Typography component="h2" variant="h4" color="textPrimary">
                      {tier.price}
                    </Typography>
                    <Typography variant="h6" color="textSecondary">
                      تومان/{tier.duration} روز
                    </Typography>
                  </div>
                  <Typography variant="h6" align="center" gutterBottom>
                    اجازه دسترسی به :
                  </Typography>
                  <ul>
                    {tier.description.map((line) => (
                      <Typography
                        component="li"
                        variant="subtitle1"
                        align="center"
                        key={line}
                      >
                        {line}
                      </Typography>
                    ))}
                  </ul>
                </CardContent>
                <CardActions>
                  <Button
                    fullWidth
                    // @ts-ignore
                    variant={tier.buttonVariant}
                    color="secondary"
                  >
                    {tier.buttonText}
                  </Button>
                </CardActions>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Container>
      {/* Footer */}
      <Container maxWidth="md" component="footer" className={classes.footer}>
        <Grid container spacing={4} justify="space-evenly">
          <Grid item xs={6} sm={3}>
            <Typography variant="h6" color="textPrimary" gutterBottom>
              نماد اطمینان
            </Typography>
            <a href="https://trustseal.enamad.ir/?id=195852&amp;Code=t2sG45wmVWgNnWQcW8qj">
              <img
                src="https://Trustseal.eNamad.ir/logo.aspx?id=195852&amp;Code=t2sG45wmVWgNnWQcW8qj"
                alt=""
                className={classes.namad}
                id="t2sG45wmVWgNnWQcW8qj"
              />
            </a>
          </Grid>
          <Grid item xs={6} sm={3}>
            <Typography variant="h6" color="textPrimary" gutterBottom>
              زرین پال
            </Typography>
            <img
              className={classes.namad}
              width="100"
              src="https://cdn.zarinpal.com/badges/trustLogo/1.svg"
              alt="Ø¯Ø±ÙˆØ§Ø²Ù‡ Ù¾Ø±Ø¯Ø§Ø®Øª Ù…Ø¹ØªØ¨Ø±"
            />
          </Grid>
        </Grid>
      </Container>
      {/* End footer */}
    </React.Fragment>
  );
}
